import styled, { css, keyframes } from 'styled-components';
import { fadeInUp } from '../MiddleBlock/styles'; // Import only fadeInUp if it's being used here

// Define the keyframes directly in this file if they're unique to VideoBackground
const slideInLeft = keyframes`
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
`;

const fadeInLetters = keyframes`
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

// Define your generateDelay function
const generateDelay = (index: number, baseDelay = 0.8) => css`
  animation-delay: ${index * baseDelay}s;
`;

export const VideoContainer = styled.div`
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 500px;
  margin-left: calc(-50vw + 50%);
  margin-bottom: 50px; // Default margin for desktop

  // Add a media query for mobile devices
  @media only screen and (max-width: 768px) {
    margin-bottom: -220px; // Smaller margin for mobile devices
  }

  // If needed, add another media query for tablets or other specific sizes
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    margin-bottom: -150px; // If you want a different margin for tablets
  }
`;

export const Video = styled.video`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -100;

  @media only screen and (max-width: 768px) {
    width: 100%;
    height: auto;
  }
`;

export const OverlayText = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  z-index: 100;

  @media only screen and (max-width: 768px) {
    top: 23%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`;

export const Title = styled("h6")`
  font-size: clamp(1.3rem, 4vw, 4em);
  font-weight: bold;
  background: -webkit-linear-gradient(right, #ffffff, #ffffff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 1px #0048b2;
  text-align: center;
  margin: 0 auto;
  margin-top: 0em;

  .word {
    display: inline-block;
    opacity: 0;
  }

  &.inView .word {
    animation: ${fadeInUp} 1.5s ease forwards;
  }

  ${Array.from({ length: 50 }, (_, index) => css`
    .word-${index} {
      ${generateDelay(index)};
    }
  `)}

  // Styles for screens larger than 1024px
  @media only screen and (min-width: 1024px) {
    text-align: left;
    // You might want to adjust margin-top here if needed for larger screens
  }

  // Styles for screens between 768px and 1024px
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    margin-bottom: .2em; // Sets the top margin to 1em for tablet devices
  }

  // Styles for screens up to 768px
  @media only screen and (max-width: 768px) {
    font-size: 1.5em; // Adjust this value as needed
    text-align: center; // Keep text aligned at center on smaller screens
    margin-bottom: .4em; // Spacing below the title for smaller screens
  }
`;

export const Content = styled("div")`
  padding: clamp(0.5rem, 2vw, 1rem) 0;
  font-size: clamp(1rem, 2.5vw, 1.5em);

  &.inView .paragraph {
    display: inline-block;
    opacity: 0;
    animation: ${slideInLeft} 2s ease forwards; /* Increased duration */
  }

  ${Array.from({ length: 10 }, (_, index) => css`
    .paragraph-${index} {
      ${generateDelay(index, 2)}; /* Increased delay for each paragraph */
    }
  `)}

  &.inView .letter {
    display: inline-block;
    opacity: 0;
    animation: ${fadeInLetters} 0.5s ease forwards; /* Increased duration */
  }

  ${Array.from({ length: 400 }, (_, index) => css`
    .letter-${index} {
      ${generateDelay(index, 0.5)}; /* Adjusted delay for each letter */
    }
  `)}

  @media only screen and (max-width: 768px) {
    display: none;
  }
`;