import React from 'react';
import { VideoContainer, Video } from './styles';

interface VideoBackgroundSportsProps {
  videoSrc: string;
}

const VideoBackgroundSports: React.FC<VideoBackgroundSportsProps> = ({ videoSrc }) => {
  return (
    <VideoContainer>
      <Video autoPlay loop muted>
        <source src={videoSrc} type="video/mp4" />
        Your browser does not support the video tag.
      </Video>
    </VideoContainer>
  );
};

export default VideoBackgroundSports;